import quickSkeleton from '@/components/skeleton/skeleton';
export default {
	data() {
		return {
			showSkeleton: true,
		};
	},
	components: {
		quickSkeleton
	},
	onReady() {
		let _this = this;
		_this.$refs.skeleton.attachedAction();
		_this.$refs.skeleton.readyAction();
		setTimeout(function() {
			_this.showSkeleton = false;
		}, 3000);
	},
}
